/* ============================================================ //
// @ module
// ============================================================ */

$to-top-color : #444;

.to-top{
  display: block;
  position: fixed;
  z-index: 99999;
  bottom: 20px;
  right: 20px;
  text-decoration: none;
  border:1px solid $to-top-color;
  border-radius:44px;
  width: 44px;
  height: 44px;
  background: #fff;
  text-indent: -9999px;
  opacity:.9;
  @include transition-delay();
  &:link,
  &:active,
  &:hover,
  &:visited,
  &:focus{
    color: $to-top-color;
    text-decoration: none;
  }
  &:before,
  &:after{
    display: block;
    content: '';
    background: $to-top-color;
    width: 10px;
    height: 1px;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin:auto;
  }
  &:before{
    transform:translate(4px,-1px) rotate(40deg);
  }
  &:after{
    transform:translate(-4px,-1px) rotate(-40deg);
  }
  &:hover{
    background: #333;
    &:before,
    &:after{
      background: #fff;
    }
  }
}

// @ loader
// ------------------------------------------------------------

.loader-layer{
  overflow: hidden;
  position: fixed;
  z-index: 100000;
  top: 0;
  left: 0;
  background: #fff;
  width: 100%;
  height:100vh;
  &__container{
  position: absolute;
  top:47%;
  left:50%;
  transform: translate(-50%, -50%);
    text-align: center;
  }
}
.loader{
  margin-left: 30px;
}

.loader-layer__logo{
  width: 140px;
  vertical-align: bottom;
}